.catalog {
  margin-bottom: 24px;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(1, 1fr);
  column-gap: 16px;
  row-gap: 40px;

  @include onTablet {
    margin-bottom: 40px;
    justify-content: space-between;
    grid-template-columns: repeat(2, 1fr);
  }

  @include onLaptop {
    justify-content: space-between;
    grid-template-columns: repeat(3, 1fr);
  }

  @include onDesktop {
    justify-content: space-between;
    grid-template-columns: repeat(4, 1fr);
  }
}
